<template name='infoWay'>
	<view class='layout-way'>
		<view class='way-line' :style='{background:Way.LineColor}'>
			<view class='mmflex-center'>
				<text class='way-line-text mmfont-17sb'>{{Way.Line}}</text>
			</view>
		</view>
		<view class='way-way'>
			<view class='mmflex-start'>
				<text class='way-way-value mmfont-17sb'>{{Way.Way}}</text>
				<text class='way-way-text mmfont-14sb' space='nbsp'> 方向</text>
			</view>
		</view>
		<view class='way-time'>
			<view class='mmflex-start'>
				<text class='way-text mmfont-14sb' space='nbsp' v-if='Way.Title'>{{Way.Title}}  </text>
				<text class='way-text mmfont-14sb' space='nbsp'>首 {{Way.First}}</text>
				<text class='mmsplit' space='nbsp'> | </text>
				<text class='way-text mmfont-14sb' space='nbsp'>末 {{Way.Last}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default({
		name: "infoWay",
		props: {
			data: {
				type: Object,
				default: function() {
					return {};
				}
			}
		},
		mounted() {
			console.log("infoWay父组件data定义的变量", this.data);
			console.log("data", this.Way);
		},
		data() {
			return {
				Way: this.data.Way
			}
		}
	})
</script>

<style>
	.mmflex-center {
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: center;
	}
	.mmflex-start {
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: flex-start;
	}
	.mmfont-17sb {
		font-size: 17px;
		font-weight: 600;
	}
	.mmfont-14sb {
		font-size: 14px;
		font-weight: 600;
	}
	.mmsplit {
		font-size: 17px;
		font-weight: 600;
		line-height: 20px;
		color: #CCCCCC;
	}
	.layout-way {
		position: relative;
		width: 100%;
		height: 56px;
	}
	.way-line {
		position: absolute;
		left: 0px;
		top: 16px;
		height: 32px;
		border-radius: 4px;
	}
	.way-line-text {
		margin-left: 12px;
		margin-right: 12px;
		line-height: 20px;
		color: white;
	}
	.way-way {
		position: absolute;
		left: 92px;
		top: 12px;
		height: 20px;
	}
	.way-way-value {
		line-height: 20px;
		color: black;
	}
	.way-way-text {
		line-height: 20px;
		color: #83929E;
	}
	.way-time {
		position: absolute;
		left: 92px;
		top: 36px;
		height: 20px;
	}
	.way-text {
		line-height: 20px;
		color: #83929E;
	}
</style>
